  padding-right: 20px;
}
.mobile-block {
  display: none;
}
.desktop-block {
  display: block;
}
.warning__input:-moz-ui-invalid,
.phone-mask:-moz-ui-invalid {
  box-shadow: none;
}
.subtitle {
  position: relative;
  display: inline-block;
  font-weight: 700;
  font-size: 1.8rem;
}
.subtitle::after {
  content: '';
  position: absolute;
  bottom: 0.6rem;
  height: 0.2rem;
  background-color: #000;
}
.link {
  position: relative;
  display: inline-flex;
  align-items: center;
  font-weight: bold;
  font-size: 1.8rem;
  cursor: pointer;
}
.link--circle::before {
  content: '';
  position: absolute;
  z-index: -1;
  top: -1.7rem;
  left: -3rem;
  width: 7rem;
  height: 7rem;
  border: 0.3rem solid #fff;
  border-radius: 50%;
}
.stroke {
  color: transparent;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: #353535;
}
.stroke--white {
  -webkit-text-stroke-color: #fff;
}
.icon-arrow-right {
