.header {
  position: relative;
}

.header__block-top {
  background-color: #F8F9FA;
}

.header__block-bottom {
  background-color: #3A3A3A;
}

.header__content-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .padding-block(1.9rem);
}

.header__logo {
  margin-left: -0.5rem;
}

.header__search {
  max-width: 826px;
  width: 59.1%;
  .margin-inline(5rem);
}

.header__menu-toggle,
.header__search-toggle {
  display: none;
}


