.projects-swiper {
  position: relative;
}

.projects-swiper__main-title {
  position: relative;
  z-index: 2;
  top: 9.8rem;
  // max-width: 870px;
  max-width: 920px;

  font-weight: 700;
  font-size: 8rem;
  text-transform: uppercase;
}

.projects-swiper__container {
  padding-top: 20.5rem;
}

.projects-swiper__link {
  width: @a;
  margin-bottom: 0;
}

.projects-swiper__arrows-wrap {
  top: 2rem;
}

